#root{overflow-x:hidden;width:100vw;height:100vh}.App{width:calc( 100vw - 50px)}input{background-color:#fff;background-clip:border-box;border:1px solid rgba(0,0,0,0.125);border-radius:.25rem}.card-header{display:flex;align-items:flex-start;justify-content:space-between}@media (min-width: 576px){.card-columns{-webkit-column-count:2;column-count:2}}.modal-dialog{max-width:50vw}.Power{background-color:#fde3e3;background:linear-gradient(to bottom, #fde3e3, #fcc)}.Power-Header{background-color:coral}.Finesse{background-color:lightgrey}.Finesse-Header{background-color:grey}.Resilience{background-color:#e3c4a6}.Resilience-Header{background-color:#e07325}.Intellect{background-color:#c19dfb}.Intellect-Header{background-color:purple}.Wits-Header{background-color:#e2dfdf}.Wits{background-color:whitesmoke}.Presence-Header{background-color:gold}.Presence{background-color:lightyellow}

.dialog{background-color:#fff;border-radius:5px;max-width:530px;min-height:300px;margin:0 auto;padding:30px;overflow-y:scroll}.dialog-backdrop{position:fixed;top:0;bottom:0;left:0;right:0;background-color:rgba(0,0,0,0.3);padding:5px}.modal-content{overflow-y:scroll;max-height:calc(100vh - 1.75rem)}

.attribute-widget {
  min-width: 300px;
}

.attribute-grid {
    width: 100%;
}

.attribute-base {
    max-width: 50px;
}
.skill-widget {
  min-width: 350px;
}

.skill-grid {
    width: 100%;
}
.skill-delete {
    width: 25px;
}

.skill-name-input {
    width: 100%;
}

.skill-add {
  text-align: center;
  padding-top: 10px;
}

.blessing-widget{min-width:530px}.blessing-grid{width:100%}.blessing-header th,.blessing-row td{padding-right:5px}.blessing-row{text-align:center}.blessing-add{text-align:center;padding-top:10px}

.sidebar{position:fixed;width:150px;height:100vh;color:white;left:-110px;transition:left 0.5s}.sidebar .menu-icon{margin-left:120px}.sidebar ul{list-style-type:none;-webkit-padding-start:15px;padding-inline-start:15px}.sidebar ul li{min-width:100px}.sidebar>ul{padding-top:15px;-webkit-margin-before:0px;margin-block-start:0px;-webkit-margin-after:0px;margin-block-end:0px;-webkit-padding-start:10px;padding-inline-start:10px}.sidebar>*:first-child{margin-top:10px}.sidebar .nav-item{cursor:pointer}.sidebar+*{margin-left:40px;transition:margin-left 0.5s}.sidebar:hover{left:0px}.sidebar:hover+*{margin-left:150px}

.DicePoolSelector{display:grid;grid-template-areas:'header header' ''}

.ChartSelector{display:inline-flex;padding:5px}.ChartSelector>*{margin:5px}

